import { Component } from "react";
import ShopingForm from "./ShoppingForm";

class Shoping extends Component {
  constructor(props) {
    super(props);
    this.state = {
      items: [
        { name: "milk", qty: "2 gallons" },
        { name: "Bread", qty: "2 Loaves" }
      ]
    };
    this.addItem = this.addItem.bind(this);
  }

  addItem(item) {
    this.setState((state) => ({
      items: [...state.items, item]
    }));
  }

  renderItme() {
    return (
      <ul>
        {this.state.items.map((item) => (
          <li>
            {item.name}, {item.qty}
          </li>
        ))}
      </ul>
    );
  }
  render() {
    return (
      <div>
        <h1>Shoping List</h1>
        {this.renderItme()}
        <ShopingForm addItem={this.addItem} />
      </div>
    );
  }
}

export default Shoping;
